*{
  margin:0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
display:flex;
align-items:center;
color:white;
background-color:black;
top:0;
position:-webkit-sticky;
position:sticky;
z-index:2000;
}
.header_left{
	flex:0.4 1;
}
.header_left>h1{
text-align:center;
font-family:"Alata",sans-serif;
letter-spacing:1px;
padding:20px 0;
}
.header_left>h1>span{
	color:red;
}
.header_right{
display:flex;
flex:0.6 1;
justify-content:space-evenly;
align-items: center;
margin-top:30px;

}
.header_right>a>h4{
font-size:18px;
cursor:pointer;
transition:transfrom 200ms ease-in-out;
font-family:"Crimson Text",serif;

}
.header_right >a>h4:hover{
transform:scale(1.09);
background-color:#f64c08;
padding:5px 20px;
border-radius:2px;
letter-spacing:1px;
font-size:18px;
cursor:pointer;
}
.heder_button{
background-color:#f64c08;
color:white;
padding:5px 20px;
cursor:pointer;
height:22px
}

.topcontent{
color:white;
}
.topContent_container{
position:absolute;
top:280px;
left:100px;
}
.topContent_container> p{
letter-spacing:1px;
font-weight:700;
margin-bottom:20px;
padding-right:10px;
padding-top:20px;
color :white;
font-size:25px
}

.topContent_container>a>button{
 margin-bottom:30px;
 text-align:left;
 border:none;
 border-radius:2px;
 outline:none;
}
.topContent_container>h1 {
font-size:50px;
font-family:"Monstserrat",sans-serif
}
.topcontent_dounloadbutton{
	color:white;
	background-color:#ff4500;
	padding:10px 30px;
	font-size:14px;
	font-family:"Monstserrat",sans-serif;
	font-weight:300;
    cursor:pointer;
	margin-right:20px;
}
.topcontent_dounloadbutton:hover{
	background-color :white;
	color:#ff4500;

}

.topcontent_workbutton{
color:black;
background-color:transparent;
font-size:14px;
padding:10px 30px;
font-weight:700;
cursor:pointer;
border:1px solid white!important;
}
.topcontent_workbutton:hover{
background-color:white;
color:#ff4500;
}
